<template>
  <div class="headerWrapper">
    <header class="header">
      <div class="logo">
        <img src="../../assets/logo.png" alt>
      </div>
      <div class="rightBox">
        <!-- 右边的操作 -->
        <div class="boxItem">
          <i class="iconfont iconfangzi icon"></i>
          <span>商店名称</span>
        </div>
        <div class="boxItem">
          <el-dropdown trigger="click">
            <span class="el-dropdown-link">
              <i class="iconfont iconuser-3 icon"></i>
              用户名
              <i class="el-icon-arrow-down el-icon--right icon"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item style="width:50px;text-align:center;"><span @click="signOut">退 出</span></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </header>
    <div class="menu-box">
      <menu-tabs :tabList="tabList"></menu-tabs>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import MenuTabs from '@/components/MenuTabs/MenuTabs'
import {auth} from '../../../static/auth'
export default {
  data () {
    return {
      tabList: []
    }
  },
  created () {
    // axios.get("/static/auth.json", {}).then(res => {
    //   this.tabList = res.data.data
    // });
     this.tabList = auth
  },
  components: {
    MenuTabs
  },
  methods:{
    //退出
    signOut(){
      this.$router.push('/login');
    }
  }
}
</script>

<style lang="scss" scoped>
.header {
  width: 100vw;
  height: 60px;
  background: #ccc;
  position: fixed;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .logo {
    width: 180px;
    height: 100%;
    img {
      display: block;
      width: 140px;
      margin: auto;
      height: 50px;
      margin-top: 5px;
    }
  }
  .rightBox {
    margin-right: 20px;
    display: flex;
    align-items: center;
    .boxItem{
      margin: 0 10px;
      cursor: pointer;
      .icon{
        color: #008cff;
      }
    }
  }
}
</style>

<style>
.headerWrapper .el-dropdown{
  color: #000;
  font-size: 16px;
}

</style>



