<template>
  <div class="main">
    <div class="bg"></div>
    <div class="leftTab">
      <el-row class="tac">
        <el-col :span="24">
          <el-menu
            class="el-menu-vertical-demo"
            :unique-opened="true"
            @open="handleOpen"
            @close="handleClose"
            :default-active="$route.path"
            router
          >
            <!--<template v-for="v in tabList">-->
            <!--<el-submenu :index="v.entity.id +''" :key="v.entity.id" v-if="v.childrenList">-->
            <!--<template slot="title">-->
            <!--<span>-->

            <!--{{v.entity.nodeName}}-->
            <!--</span>-->
            <!--</template>-->
            <!--<el-menu-item-group v-for="j in v.childrenList" :key="j.entity.id">-->
            <!--<el-menu-item :index="j.entity.url">{{j.entity.nodeName}}</el-menu-item>-->
            <!--</el-menu-item-group>-->
            <!--</el-submenu>-->
            <!--&lt;!&ndash;lpffff&ndash;&gt;-->

            <!--<el-menu-item :index="v.entity.url +''" :key="v.entity.id" v-else>-->
            <!--<span slot="title">{{v.entity.nodeName}}</span>-->
            <!--</el-menu-item>-->
            <!--</template>-->
            <template v-for="v in tabList">
              <el-submenu :index="v.id +''" :key="v.id" v-if="v.children">
                <template slot="title">
                  <span>{{v.text}}</span>
                </template>
                <el-menu-item-group v-for="j in v.children" :key="j.id">
                  <el-menu-item :index="j.path">{{j.text}}</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <!--lpffff-->

              <el-menu-item :index="v.path +''" :key="v.id" v-else>
                <span slot="title">{{v.text}}</span>
              </el-menu-item>
            </template>
          </el-menu>
        </el-col>
      </el-row>
    </div>
    <div class="contentDetail">
      <router-view></router-view>
    </div>
  </div>
</template>

<style scoped lang="scss">
.el-menu {
  // border: none;
}
.bg {
  width: 180px;
  height: 100vh;
  position: fixed;
  background: #fff;
  top: 0;
}
/* 由于属性兼容问题，选择适用父元素和子元素之间的关系设置样式来隐藏滚动条*/
/* 大致思路就是在内容div外面包一个父容器div，设置 overflow: hidden，
  内容div设置 overflow-x: hidden; overflow-y: auto;
  最后设置父容器div的宽度小于内容div的宽度或者
  设置内容div的 margin-right 为负值就可以了。
  */
.main {
  width: 100%;
  position: absolute;
  top: 60px;
  bottom: 0;
  overflow: hidden;
}
.leftTab {
  width: 180px;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
.leftTab::-webkit-scrollbar {
  /* 这个属性只适用于 Chrome 和 Safari */
  display: none;
}
.el-menu-item-group__title {
  padding: 0;
}
.contentDetail {
  padding: 30px;
  padding-left: 0px;
  // padding-right: 10px;
  position: absolute;
  right: 0;
  top: 0px;
  left: 200px;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: auto;
}
.contentDetail::-webkit-scrollbar {
  /* 这个属性只适用于 Chrome 和 Safari */
  display: none;
}

.leftTab,
contentDetail {
  -ms-scroll-chaining: chained;
  -ms-overflow-style: none;
  -ms-content-zooming: zoom;
  -ms-scroll-rails: none;
  -ms-content-zoom-limit-min: 100%;
  -ms-content-zoom-limit-max: 500%;
  -ms-scroll-snap-type: proximity;
  -ms-scroll-snap-points-x: snapList(100%, 200%, 300%, 400%, 500%);
  -ms-overflow-style: none;
  overflow: auto;
}
</style>

<script>
export default {
  props: [
    "tabList"
  ],
  methods: {
    handleOpen (key, keyPath) {
      // console.log(key, keyPath);
    },
    handleClose (key, keyPath) {
      // console.log(key, keyPath);
    }
  }
}
</script>



